@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Inter', sans-serif;
    background-color: #141414;
    color: #fff;
    text-align: center;
    margin: 0;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: none;
}

h1 {
    color: #fff;
    font-size: 3rem;
}

button {
    background-color: #007bff;
    font-size: 1.25rem;
    color: #fff;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    margin-top: 10px;
    border-radius: 5px;
    transition: .2s;

    &:hover {
        background-color: #0056b3;
    }
}

.container {
    display: flex;
    gap: 20px;
}

.image-wrapper {
    position: relative;

    img {
        display: block;
    }
}

.cursor {
    position: fixed;
    pointer-events: none;
    z-index: 1000;
    transition: opacity 0.3s ease, transform 0.3s ease;
}

.cursor__dot {
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background-color: #e92b2b;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    opacity: 1;
    transition: width 0.3s ease, height 0.3s ease;
}

.cursor__text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0, 0);
    text-align: center;
    font-size: 16px;
    text-transform: uppercase;
    color: #fff;
    opacity: 0;
    transition: opacity 0.3s ease, transform 0.3s ease;
}

.cursor.large .cursor__dot {
    width: 72.5px;
    height: 72.5px;
}

.cursor.large .cursor__text {
    opacity: 1;
    transform: translate(-50%, -50%) scale(1, 1);
}

.buttons-wrapper {
    width: 100%;
    margin: 10vh auto;
    padding: 17.5vh 32.5%;
    background-color: rgb(225, 225, 225);
    display: flex;
    justify-content: space-between;
    align-items: center;

    .button {
        font-family: 'Inter', sans-serif;
        color: #000;
        background-color: #fff;
        text-transform: uppercase;
        border-radius: 100px;
        position: relative;
        overflow: hidden;
        cursor: pointer;
        padding: 20px 35px;
    }

    .button-1 {
        display: flex;
        align-items: center;
        gap: 15px;

        .rounded {
            width: 8px;
            height: 8px;
            border-radius: 50%;
            background-color: #000;
            transition: all .3s ease-out;
        }

        .primary {
            margin: 0;
            font-size: 16px;
            transition: all .3s ease-out;
            z-index: 2;
        }

        .secondary {
            position: absolute;
            left: 100%;
            transition: transform .3s ease-out;
            z-index: 2;
            color: #fff;
            /*top: 22.5px;*/
        }

        &:hover {
            .rounded {
                transform: scale(40);
                background-color: #0a00c1;
            }

            .primary {
                color: #fff;
                transform: translateX(-25px);
            }

            .secondary {
                transform: translateX(-50px);
            }
        }
    }

    .button-2 {
        .primary {
            top: 0;
            position: relative;
            transition: top .4s cubic-bezier(.33, 1, .68, 1);
        }

        .secondary {
            position: absolute;
            top: 110%;
            left: 0;
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: top .4s cubic-bezier(.33, 1, .68, 1);

            p {
                position: absolute;
            }

            div {
                background-color: #c9ff18;
                width: 60%;
                height: 100%;
                border-radius: 50%;
                transition: all .4s cubic-bezier(.33, 1, .68, 1);
            }
        }

        &:hover {
            .primary {
                top: -40px;
            }

            .secondary {
                top: 0;

                div {
                    width: 100%;
                    border-radius: 100px;
                }
            }
        }
    }

    .button-3 {
        padding: 20px 0;

        .mask {
            overflow: hidden;
            position: relative;

            .slider {
                display: flex;
                position: relative;
                animation: text_slide 3.75s linear 0s infinite;
                transition: top .5s ease-out;
                top: 0;

                p {
                    margin: 0;
                    text-transform: uppercase;
                    white-space: nowrap;

                    &:nth-of-type(2) {
                        position: absolute;
                        left: calc(100% + 15px);
                    }
                }
            }

            .hover-text {
                position: absolute;
                top: 0;
                left: 50%;
                transform: translateX(-50%);

                span {
                    position: relative;
                    color: transparent;
                    top: 18px;
                    transition: top .25s ease-out;
                }
            }
        }

        &:hover {
            background-color: #000;

            .slider {
                transition: top .75s ease-out;
                top: -18px;
            }

            .hover-text span {
                color: #fff;
                top: 0;
            }
        }
    }
}

@keyframes text_slide {
    100% {
        transform: translateX(calc(-100% - 15px));
    }
}

@media screen and (max-width: 1100px) {
    body {
        cursor: auto;
    }

    .image-wrapper {
        img {
            display: none;
        }
    }

    .cursor {
        display: none
    }
}